import * as React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout/Layout"
import Seo from "../components/seo/Seo"
import ModuleCard from "../components/courses/ModuleCard"
import CourseBreadcrumb from "../components/courses/CourseBreadcrumb"

const CourseDetailTemplate = ({ data, pageContext }) => {
    const { courseData } = pageContext
    const modules = data.allMdx.nodes

    return (
        <Layout>
            <div className="max-w-[1200px] mx-auto px-4 py-12">
                <CourseBreadcrumb courseTitle={courseData.title} courseSlug={courseData.slug} />

                {/* Course Header */}
                <div className="mb-12">
                    <h1
                        className="mb-4 text-[var(--color-editorial-black)]"
                        style={{ fontSize: "var(--text-3xl)", fontWeight: 600, lineHeight: "1.2" }}
                    >
                        {courseData.title}
                    </h1>

                    {courseData.description && (
                        <p
                            className="mb-6 text-[var(--color-editorial-grey)] max-w-3xl"
                            style={{ fontSize: "var(--text-lg)", lineHeight: "1.6" }}
                        >
                            {courseData.description}
                        </p>
                    )}

                    <div className="flex items-center gap-6 flex-wrap">
                        <div className="flex items-center gap-2">
                            <span style={{ fontSize: "var(--text-2xl)" }}>üìö</span>
                            <span className="text-[var(--color-editorial-grey)]" style={{ fontSize: "var(--text-sm)" }}>
                                {courseData.totalModules} Modules
                            </span>
                        </div>
                        {courseData.level && (
                            <div className="flex items-center gap-2">
                                <span style={{ fontSize: "var(--text-2xl)" }}>üéØ</span>
                                <span className="text-[var(--color-editorial-grey)]" style={{ fontSize: "var(--text-sm)" }}>
                                    {courseData.level}
                                </span>
                            </div>
                        )}
                        {courseData.duration && (
                            <div className="flex items-center gap-2">
                                <span style={{ fontSize: "var(--text-2xl)" }}>‚è±</span>
                                <span className="text-[var(--color-editorial-grey)]" style={{ fontSize: "var(--text-sm)" }}>
                                    {courseData.duration}
                                </span>
                            </div>
                        )}
                    </div>
                </div>

                {/* Modules Grid */}
                <div>
                    <h2
                        className="mb-6 text-[var(--color-editorial-black)]"
                        style={{ fontSize: "var(--text-2xl)", fontWeight: 600 }}
                    >
                        Course Modules
                    </h2>

                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                        {modules.map((module, index) => (
                            <ModuleCard
                                key={module.id}
                                module={{
                                    ...module.frontmatter,
                                    slug: module.fields.slug,
                                }}
                                courseSlug={courseData.slug}
                                index={index}
                            />
                        ))}
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export const Head = ({ pageContext }) => {
    const { courseData } = pageContext
    return <Seo title={courseData.title} description={courseData.description} />
}

export default CourseDetailTemplate

export const pageQuery = graphql`
  query CourseDetailQuery($courseSlug: String!) {
    allMdx(
      filter: { frontmatter: { courseSlug: { eq: $courseSlug } } }
      sort: { frontmatter: { order: ASC } }
    ) {
      nodes {
        id
        fields {
          slug
        }
        frontmatter {
          title
          description
          order
          duration
        }
      }
    }
  }
`
